@charset "UTF-8";
/*  ==========================================================================
    Base styles
    ========================================================================== */
html {
  font-size: 14px;
  line-height: 21px;
  height: 100%;
}

body {
  font-family: "OpenSansRegular", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  position: relative;
  height: 100%;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
}

* {
  outline: none !important;
}

*:focus {
  outline: none !important;
}

a {
  text-decoration: none;
  -webkit-transition: 300ms false false;
  -moz-transition: 300ms false false false;
  -o-transition: 300ms false false false;
  transition: 300ms;
}

a:link, a:visited, a:hover, a:active {
  text-decoration: none;
}

b, strong, dt {
  font-family: "OpenSansSemibold";
}

/*
    Headings
    ========================================================================== */
h1, .h1 {
  font-size: 36px;
  font-size: 2.57143rem;
}

h2, .h2 {
  font-size: 30px;
  font-size: 2.14286rem;
}

h3, .h3 {
  font-size: 24px;
  font-size: 1.71429rem;
}

h4, .h4 {
  font-size: 18px;
  font-size: 1.28571rem;
}

h5, .h5 {
  font-size: 14px;
  font-size: 1rem;
}

h6, .h6 {
  font-size: 12px;
  font-size: 0.85714rem;
}

/*
    Selection Styling
    ========================================================================== */
::-moz-selection {
  text-shadow: none;
}

::selection {
  text-shadow: none;
}

/*
    Styled horizontal rule
    ========================================================================== */
hr {
  border: 0;
  display: block;
  height: 1px;
  padding: 0;
  margin: 10px 0;
}

/*
    Browse Happy prompt
    ========================================================================== */
.browsehappy {
  text-align: center;
  padding: 10px 0;
}

/*
    Overwrites
    ========================================================================== */
.dropdown-menu {
  font-size: 13px;
  font-size: 0.92857rem;
}

.form-control {
  font-size: 14px;
  font-size: 1rem;
}

/*  ==========================================================================
    Layout
    ========================================================================== */
/*
    Page Layouts - General Containers for Site Layout
    ========================================================================== */
/*
	Main Containers
	-------------------------------------  */
.l-main-container {
  display: table;
  position: relative;
  height: 100%;
  width: 100%;
  table-layout: fixed;
}

.l-container {
  display: table-cell;
  width: 100%;
  height: 100%;
}

/*
	Boxed Layout
	-------------------------------------  */
.l-boxed {
  width: auto;
  max-width: 1378px;
  float: none;
  display: block;
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.section-title .view-all {
  float: right;
}

.section-title .slider-nav {
  float: right;
}

.section-title .slider-nav li {
  display: inline-block;
}

/*
	Slide - Header, Footer, Side - Push Helper
	-------------------------------------  */
.l-slide-push {
  overflow-x: hidden;
  position: relative;
  left: 0;
}

/*
	Animation
	-------------------------------------  */
.anim-off {
  display: none;
}

/*
    Element Spacing
    ------------------------------------- */
/* Margin */
.l-spaced {
  margin: 2%;
}

.l-spaced-left {
  margin-left: 2%;
}

.l-spaced-right {
  margin-right: 2%;
}

.l-spaced-top {
  margin-top: 2%;
}

.l-spaced-bottom {
  margin-bottom: 2%;
}

.l-spaced-half {
  margin: 1%;
}

.l-spaced-vertical {
  margin-top: 2%;
  margin-bottom: 2%;
}

.l-spaced-horizontal {
  margin-left: 2%;
  margin-right: 2%;
}

/* Padding */
.l-spaced-p {
  padding: 2%;
}

.l-spaced-p-left {
  padding-left: 2%;
}

.l-spaced-p-right {
  padding-right: 2%;
}

.l-spaced-p-top {
  padding-top: 2%;
}

.l-spaced-p-bottom {
  padding-bottom: 2%;
}

.l-spaced-p-half {
  padding: 1%;
}

.l-spaced-p-vertical {
  padding-top: 2%;
  padding-bottom: 2%;
}

.l-spaced-p-horizontal {
  padding-left: 2%;
  padding-right: 2%;
}

/*
    Page Heading
    ------------------------------------- */
.l-page-header {
  padding: 0 2%;
  overflow: hidden;
  *zoom: 1;
}

.l-page-title {
  font-family: "OpenSansLight";
}

.l-page-title span {
  font-family: "OpenSansBold";
}

/*
    Scroll Up
    ------------------------------------- */
#scrollUp-active {
  display: none;
}

#scrollUp {
  position: fixed;
  right: 20px;
  bottom: 43px;
  z-index: 100;
  text-align: center;
  opacity: 0.6;
  padding: 10px 15px;
  display: block;
  font-size: 16px;
  font-size: 1.14286rem;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
  border-radius: 4px;
}

#scrollUp:hover {
  opacity: 0.8;
}

