/*
    FancySelect
    ------------------------------------- */
div.fancy-select {
  position: relative;
  text-transform: uppercase;
  font-size: 13px;
  font-size: 0.92857rem;
}

div.fancy-select.disabled {
  opacity: 0.5;
}

div.fancy-select div.trigger {
  cursor: pointer;
  padding: 7px 24px 4px 9px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  position: relative;
  width: 200px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
  border-radius: 4px;
  -webkit-transition: all 240ms ease-out;
  -moz-transition: all 240ms ease-out false;
  -o-transition: all 240ms ease-out false;
  transition: all 240ms ease-out;
}

div.fancy-select div.trigger:after {
  content: "";
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  border: 5px solid transparent;
  top: 14px;
  right: 10px;
}

div.fancy-select ul.options {
  list-style: none;
  margin: 0;
  position: absolute;
  top: 40px;
  left: 0;
  visibility: hidden;
  opacity: 0;
  z-index: 50;
  max-height: 200px;
  overflow: auto;
  min-width: 200px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
  border-radius: 4px;
  -webkit-transition: opacity 300ms ease-out, top 300ms ease-out, visibility 300ms ease-out;
  -moz-transition: opacity 300ms ease-out false, top 300ms ease-out false, visibility 300ms ease-out false;
  -o-transition: opacity 300ms ease-out false, top 300ms ease-out false, visibility 300ms ease-out false;
  transition: opacity 300ms ease-out, top 300ms ease-out, visibility 300ms ease-out;
}

div.fancy-select ul.options.open {
  visibility: visible;
  top: 40px;
  opacity: 1;
  /* have to use a non-visibility transition to prevent this iOS issue (bug?): */
  /*http://stackoverflow.com/questions/10736478/css-animation-visibility-visible-works-on-chrome-and-safari-but-not-on-ios*/
  -webkit-transition: opacity 300ms ease-out, top 300ms ease-out;
  -moz-transition: opacity 300ms ease-out false, top 300ms ease-out false;
  -o-transition: opacity 300ms ease-out false, top 300ms ease-out false;
  transition: opacity 300ms ease-out, top 300ms ease-out;
}

div.fancy-select ul.options.overflowing {
  top: auto;
  bottom: 40px;
  -webkit-transition: opacity 300ms ease-out, bottom 300ms ease-out, visibility 300ms ease-out;
  -moz-transition: opacity 300ms ease-out false, bottom 300ms ease-out false, visibility 300ms ease-out false;
  -o-transition: opacity 300ms ease-out false, bottom 300ms ease-out false, visibility 300ms ease-out false;
  transition: opacity 300ms ease-out, bottom 300ms ease-out, visibility 300ms ease-out;
}

div.fancy-select ul.options.overflowing.open {
  top: auto;
  bottom: 40px;
  -webkit-transition: opacity 300ms ease-out, bottom 300ms ease-out;
  -moz-transition: opacity 300ms ease-out false, bottom 300ms ease-out false;
  -o-transition: opacity 300ms ease-out false, bottom 300ms ease-out false;
  transition: opacity 300ms ease-out, bottom 300ms ease-out;
}

div.fancy-select ul.options li {
  padding: 8px 12px;
  cursor: pointer;
  white-space: nowrap;
  -webkit-transition: all 150ms ease-out;
  -moz-transition: all 150ms ease-out false;
  -o-transition: all 150ms ease-out false;
  transition: all 150ms ease-out;
}

