.rickshaw_graph .detail .item {
  position: absolute;
  z-index: 2;
  border-radius: 3px;
  padding: 0.25em;
  font-size: 12px;
  font-size: 0.85714rem;
  font-family: Arial, sans-serif;
  opacity: 0;
  margin-left: 1em;
  margin-right: 1em;
