/* annotations */
.rickshaw_annotation_timeline {
  height: 1px;
  margin-top: 10px;
  position: relative;
}

.rickshaw_annotation_timeline .annotation {
  position: absolute;
  height: 6px;
  width: 6px;
  margin-left: -2px;
  top: -3px;
  border-radius: 5px;
}

.rickshaw_graph .annotation_line {
  position: absolute;
