  cursor: pointer;
}

.rickshaw_annotation_timeline .annotation .content:before {
  content: "\25b2";
  position: absolute;
  top: -11px;
}

.rickshaw_annotation_timeline .annotation.active, .rickshaw_annotation_timeline .annotation:hover {
  cursor: none;
}

.rickshaw_annotation_timeline .annotation .content:hover {
