/*
    Effects
    ------------------------------------- */
/* --- Setup - 1 --- */
.btn-eff {
  position: relative;
  -webkit-transition: all 300ms false;
  -moz-transition: all 300ms false false;
  -o-transition: all 300ms false false;
  transition: all 300ms;
}

.btn-eff:before {
  content: '';
  position: absolute;
  -webkit-transition: all 300ms false;
  -moz-transition: all 300ms false false;
  -o-transition: all 300ms false false;
  transition: all 300ms;
}

.btn-eff span {
  position: relative;
}

/* --- Effect 2 --- */
.btn-eff-2:before {
  width: 100%;
  height: 0;
  top: 0;
  left: 0;
}

.btn-eff-2:hover:before {
  height: 100%;
}

/* --- Effect 3 --- */
.btn-eff-3:before {
  width: 0%;
  height: 100%;
  top: 0;
  left: 0;
}

.btn-eff-3:hover:before {
  width: 100%;
}

/* --- Effect 4 --- */
.btn-eff-4 {
  overflow: hidden;
}

.btn-eff-4:before {
  width: 0;
  height: 103%;
  top: 50%;
  left: 50%;
  opacity: 0;
  -webkit-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -ms-transform: translateX(-50%) translateY(-50%);
  -o-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

.btn-eff-4:hover:before {
  width: 80%;
  opacity: 1;
}

.btn-eff-4:active:before {
  width: 102%;
  opacity: 1;
}

/* --- Effect 5 --- */
.btn-eff-5 {
  overflow: hidden;
}

.btn-eff-5:before {
  width: 125%;
  height: 0;
  top: 50%;
  left: 50%;
  opacity: 0;
  -webkit-transform: translateX(-50%) translateY(-50%) rotate(45deg);
  -moz-transform: translateX(-50%) translateY(-50%) rotate(45deg);
  -ms-transform: translateX(-50%) translateY(-50%) rotate(45deg);
  -o-transform: translateX(-50%) translateY(-50%) rotate(45deg);
  transform: translateX(-50%) translateY(-50%) rotate(45deg);
}

.btn-eff-5:hover:before {
  height: 150%;
  opacity: 1;
}

.btn-eff-5:active:before {
  height: 400%;
  opacity: 1;
}

/* --- Effect 6 --- */
.btn-eff-6 {
  overflow: hidden;
}

.btn-eff-6:before {
  width: 102%;
  height: 0;
  top: 50%;
  left: 50%;
  opacity: 0;
  -webkit-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -ms-transform: translateX(-50%) translateY(-50%);
  -o-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

.btn-eff-6:hover:before {
  height: 75%;
  opacity: 1;
}

.btn-eff-6:active:before {
  height: 130%;
  opacity: 1;
}

/* --- Setup - 2 --- */
.btn-shadow {
  position: relative;
}

/* --- Effect 1 --- */
.btn-sh-1:hover {
  top: 2px;
}

.btn-sh-1:active {
  top: 6px;
}

.btn-sh-2:hover {
  top: -2px;
}

.btn-sh-2:active {
  top: 6px;
}

/* --- Setup - 3 --- */
.btn-border {
  position: relative;
  -webkit-transition: all 200ms false;
  -moz-transition: all 200ms false false;
  -o-transition: all 200ms false false;
